
.container {
  margin: 1em auto;
  text-align: center;
  font-family: monospace;
  font-size: '20px'
}

.button {
  background-color: #e91e63;
  border: none;
  color: white;
  padding: 15px 32px;
  font-size: 16px;
  cursor: pointer;
  transition-duration: 0.4s;
  font-family: monospace;
}

.button:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}